import React, { Component } from 'react'
import { Form, Input, Button } from 'antd'
import i18n from 'i18next'
import { isPhone } from '@utils/index'
import type { IUser } from '@interfaces/user'

type IUserCollectionProps = {
  callback: ({ isVisible, isRefresh, values }) => unknown
  form: any
  entity: IUser
}
type IUserCollectionState = {
  loading: boolean
}

export default class UserCollecrtionComponent extends Component<
  IUserCollectionProps,
  IUserCollectionState
> {
  constructor(props) {
    super(props)
    this.state = {
      loading: false,
    }
  }
  componentDidMount() {
    // console.log(this.props.entity);
  }
  cancel(e) {
    e.preventDefault()
    this.props.callback({
      isVisible: false,
      isRefresh: false,
      values: '',
    })
  }
  handleSubmit(values) {
    this.props.callback({
      isVisible: false,
      isRefresh: true,
      values,
    })
  }
  checkPhone(rule, value, callback) {
    isPhone(value) ? callback() : callback('请输入正确的手机号码')
  }
  render() {
    const { entity } = this.props
    return (
      <Form
        labelCol={{ span: 24 }}
        wrapperCol={{ span: 24 }}
        onFinish={this.handleSubmit.bind(this)}
        initialValues={{
          username: entity ? entity.username : '',
          password: entity ? entity.password : '',
        }}
      >
        <Form.Item
          label="用户名"
          name="username"
          rules={[{ required: true, message: '请输入用户名' }]}
        >
          <Input placeholder="请输入用户名" />
        </Form.Item>
        <Form.Item label="密码" name="password" rules={[{ required: true, message: '请输入密码' }]}>
          <Input placeholder="请输入密码" type="password" />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit" className="m-r-12">
            {i18n.t('action.save')}
          </Button>
          <Button onClick={this.cancel.bind(this)}> {i18n.t('action.cancel')}</Button>
        </Form.Item>
      </Form>
    )
  }
}
